.productpage-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1.5rem;
  font-family: "Inter", sans-serif;
  color: #1a202c;
  background-color: #fff;
}

/* === Top Bar === */
.productpage-top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.productpage-back-btn,
.productpage-quote-btn {
  border: none;
  border-radius: 8px;
  font-weight: 600;
  padding: 0.75rem 1.25rem;
  cursor: pointer;
  transition: 0.2s;
}

.productpage-back-btn {
  background: #edf2f7;
  color: #2d3748;
}

.productpage-back-btn:hover {
  background: #e2e8f0;
}

.productpage-quote-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.productpage-quote-btn:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

/* === Main Layout === */
.productpage-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

/* === Image Section === */
.productpage-image-wrapper {
  position: relative;
  background: #f9fafb;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
  overflow: hidden;
}

.productpage-image {
  width: 100%;
  height: 500px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.productpage-image:hover {
  transform: scale(1.03);
}

.productpage-variant-badge {
  position: absolute;
  top: 12px;
  left: 12px;
  background: #4299e1;
  color: white;
  padding: 0.25rem 0.75rem;
  font-size: 0.85rem;
  border-radius: 6px;
}

/* === Info Section === */
.productpage-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.productpage-name {
  font-size: 2rem;
  font-weight: 700;
}

.productpage-rating {
  display: flex;
  align-items: center;
  color: #f6ad55;
  font-size: 1.25rem;
  gap: 0.4rem;
}

.productpage-rating-text {
  color: #4a5568;
  font-size: 0.95rem;
  margin-left: 0.5rem;
}

.productpage-meta p {
  margin: 0.25rem 0;
  color: #4a5568;
}

/* === Variant Selector === */
.variant-selector {
  margin-top: 0.5rem;
}

.variant-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.variant-btn {
  border: 1px solid #cbd5e0;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  background: white;
  cursor: pointer;
  font-weight: 500;
  transition: 0.2s;
}

.variant-btn.active {
  border-color: #4299e1;
  background: #ebf8ff;
  color: #2b6cb0;
}

.variant-btn:hover {
  background: #edf2f7;
}

/* === Price & Description === */
.productpage-price {
  font-size: 2rem;
  font-weight: 700;
  color: #2f855a;
  margin: 0.5rem 0 1rem;
}

.est-price-tag {
  color: #a0aec0;
  font-size: 0.9rem;
  font-style: italic;
}

.productpage-description {
  font-size: 1.05rem;
  line-height: 1.7;
  background: #f7fafc;
  padding: 1rem;
  border-left: 4px solid #48bb78;
  border-radius: 6px;
}

/* === Quantity & Button === */
.productpage-quantity-section label {
  font-weight: 600;
}

.productpage-quantity-row {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.productpage-qty-btn {
  width: 40px;
  height: 40px;
  border: 1px solid #cbd5e0;
  border-radius: 8px;
  background: white;
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 600;
}

.productpage-qty-input {
  width: 80px;
  text-align: center;
  font-weight: 600;
  border: 1px solid #cbd5e0;
  border-radius: 8px;
}

.productpage-add-to-cart-btn {
  margin-top: 1.5rem;
  padding: 1rem;
  border: none;
  border-radius: 10px;
  background: linear-gradient(135deg, #38a169, #48bb78);
  color: white;
  font-size: 1.125rem;
  font-weight: 700;
  cursor: pointer;
  transition: 0.3s;
}

.productpage-add-to-cart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(72, 187, 120, 0.4);
}

/* === Reviews === */
.productpage-reviews-section {
  margin-top: 3rem;
  border-top: 1px solid #e2e8f0;
  padding-top: 2rem;
}

.review-card {
  background: #f8fafc;
  border-left: 4px solid #4299e1;
  padding: 1rem 1.25rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.25rem;
}

.review-stars {
  color: #f6ad55;
}

.review-buyer {
  font-weight: 600;
  color: #2d3748;
}

.review-comment {
  color: #4a5568;
  margin-top: 0.25rem;
}

/* === Add Review === */
.add-review-form {
  background: #edf2f7;
  padding: 1rem;
  border-radius: 10px;
  margin-top: 1rem;
}

.star-selector .star {
  font-size: 1.5rem;
  cursor: pointer;
  color: #cbd5e0;
}

.star.selected,
.star:hover {
  color: #f6ad55;
}

/* === Responsive === */
@media (max-width: 768px) {
  .productpage-content {
    grid-template-columns: 1fr;
  }

  .productpage-image {
    height: 350px;
  }

  .productpage-price {
    font-size: 1.75rem;
  }
}
