/* Orders Overlay */
.orderlist-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: orderlist-fadeIn 0.3s ease;
  padding: 1rem;
}

@keyframes orderlist-fadeIn {
  from { 
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  to { 
    opacity: 1;
    backdrop-filter: blur(8px);
  }
}

/* Orders Panel */
.orderlist-panel {
  width: 100%;
  max-width: 1200px;
  max-height: 90vh;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: orderlist-slideUp 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

@keyframes orderlist-slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Header - Smaller */
.orderlist-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.orderlist-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%);
}

.orderlist-header-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin: 0 0 0.25rem 0;
  font-family: 'Inter', sans-serif;
}

.orderlist-header-content p {
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
  font-size: 0.9rem;
}

.orderlist-close-header-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  flex-shrink: 0;
}

.orderlist-close-header-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

/* Summary - Smaller */
.orderlist-summary {
  display: flex;
  gap: 1.5rem;
  padding: 1rem 2rem;
  background: white;
  border-bottom: 1px solid #e2e8f0;
}

.orderlist-summary-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.orderlist-summary-label {
  font-size: 0.8rem;
  color: #64748b;
  margin-bottom: 0.25rem;
}

.orderlist-summary-value {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1e293b;
}

/* Empty State - Smaller */
.orderlist-empty {
  text-align: center;
  padding: 3rem 2rem;
  background: white;
}

.orderlist-empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.orderlist-empty h3 {
  font-size: 1.25rem;
  color: #374151;
  margin: 0 0 0.75rem 0;
}

.orderlist-empty p {
  color: #6b7280;
  margin: 0;
  font-size: 0.9rem;
}

/* Content - Grid Layout for Multiple Orders */
.orderlist-content {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
  background: #f8fafc;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 1.25rem;
  align-items: start;
}

/* Order Card - Smaller Compact Design */
.orderlist-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  border: 1px solid #f1f5f9;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  height: fit-content;
  min-height: 300px;
}

.orderlist-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.orderlist-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
}

/* Card Header - Compact */
.orderlist-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.75rem;
  gap: 0.75rem;
}

.orderlist-card-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  flex: 1;
}

.orderlist-order-id {
  font-size: 1rem;
  font-weight: 700;
  color: #1e293b;
  line-height: 1.2;
}

.orderlist-status-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.375rem 0.75rem;
  border-radius: 16px;
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
  width: fit-content;
}

.orderlist-card-meta {
  text-align: right;
  flex-shrink: 0;
}

.orderlist-total-label {
  display: block;
  font-size: 0.75rem;
  color: #64748b;
  margin-bottom: 0.125rem;
}

.orderlist-total-amount {
  font-size: 1.125rem;
  font-weight: 700;
  color: #059669;
}

/* Date Info - Smaller */
.orderlist-date-info {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  color: #64748b;
  font-size: 0.8rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  background: #f8fafc;
  border-radius: 6px;
}

.orderlist-date-icon {
  font-size: 0.9rem;
}

/* Products Section - Compact Scrollable Row */
.orderlist-products-section {
  margin-bottom: 1rem;
}

.orderlist-products-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.75rem;
  font-size: 0.9rem;
}

.orderlist-scroll-hint {
  font-size: 0.7rem;
  color: #6b7280;
  font-weight: normal;
  animation: orderlist-pulse 2s infinite;
}

@keyframes orderlist-pulse {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

.orderlist-products-scroll-container {
  position: relative;
  margin: 0 -1.5rem;
  padding: 0 1.5rem;
}

.orderlist-products-scroll {
  display: flex;
  gap: 0.75rem;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 0.5rem 0;
  scrollbar-width: thin;
  scrollbar-color: #cbd5e0 #f1f5f9;
  scroll-behavior: smooth;
}

.orderlist-products-scroll::-webkit-scrollbar {
  height: 4px;
}

.orderlist-products-scroll::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 2px;
}

.orderlist-products-scroll::-webkit-scrollbar-thumb {
  background: #cbd5e0;
  border-radius: 2px;
}

.orderlist-products-scroll::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

/* Product Card - More Compact */
.orderlist-product-card {
  flex: 0 0 auto;
  width: 140px;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 0.875rem;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  transition: all 0.2s ease;
  position: relative;
}

.orderlist-product-card:hover {
  background: #f1f5f9;
  border-color: #cbd5e0;
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.orderlist-product-image-container {
  position: relative;
  width: 100%;
  text-align: center;
}

.orderlist-product-image {
  width: 100%;
  height: 80px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.orderlist-product-quantity {
  position: absolute;
  top: -6px;
  right: -6px;
  background: #ef4444;
  color: white;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.7rem;
  font-weight: 700;
  border: 2px solid white;
}

.orderlist-product-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.orderlist-product-name {
  font-size: 0.8rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0;
  line-height: 1.3;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  min-height: 2.2em;
}

.orderlist-product-meta {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.orderlist-product-price {
  font-size: 0.75rem;
  color: #64748b;
}

.orderlist-product-subtotal {
  font-weight: 700;
  color: #059669;
  font-size: 0.8rem;
}

.orderlist-product-seller {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.7rem;
  color: #6b7280;
  margin-top: auto;
}

.orderlist-seller-icon {
  font-size: 0.65rem;
}

.orderlist-chat-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.375rem;
  padding: 0.5rem 0.75rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  margin-top: auto;
}

.orderlist-chat-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 6px rgba(59, 130, 246, 0.4);
}

.orderlist-chat-icon {
  font-size: 0.8rem;
}

/* Card Footer - Compact */
.orderlist-card-footer {
  border-top: 1px solid #e2e8f0;
  padding-top: 1rem;
  margin-top: 0.5rem;
}

.orderlist-footer-actions {
  display: flex;
  gap: 0.75rem;
}

.orderlist-track-btn,
.orderlist-details-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #e2e8f0;
  background: white;
  border-radius: 6px;
  color: #374151;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  flex: 1;
  text-align: center;
}

.orderlist-track-btn:hover,
.orderlist-details-btn:hover {
  background: #f8fafc;
  border-color: #cbd5e0;
  transform: translateY(-1px);
}

/* Footer - Smaller */
.orderlist-footer {
  padding: 1rem 2rem;
  background: white;
  border-top: 1px solid #e2e8f0;
  text-align: center;
}

.orderlist-close-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}

.orderlist-close-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
}

/* Loader - Smaller */
.orderlist-loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 2rem;
  gap: 1rem;
  grid-column: 1 / -1;
}

.orderlist-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #e2e8f0;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: orderlist-spin 1s linear infinite;
}

@keyframes orderlist-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Scrollbar for main content */
.orderlist-content::-webkit-scrollbar {
  width: 6px;
}

.orderlist-content::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}

.orderlist-content::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 3px;
}

.orderlist-content::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
}

/* Responsive Grid */
@media (max-width: 1200px) {
  .orderlist-content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .orderlist-overlay {
    padding: 0.5rem;
  }
  
  .orderlist-panel {
    max-height: 95vh;
    border-radius: 12px;
    max-width: 95vw;
  }
  
  .orderlist-header {
    padding: 1.25rem 1.5rem;
  }
  
  .orderlist-header-content h2 {
    font-size: 1.25rem;
  }
  
  .orderlist-content {
    padding: 1rem;
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .orderlist-card {
    padding: 1.25rem;
    min-height: 280px;
  }
  
  .orderlist-card-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .orderlist-card-meta {
    text-align: left;
    width: 100%;
  }
  
  .orderlist-product-card {
    width: 130px;
  }
  
  .orderlist-footer-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .orderlist-products-scroll-container {
    margin: 0 -1rem;
    padding: 0 1rem;
  }
}

@media (max-width: 480px) {
  .orderlist-overlay {
    padding: 0.25rem;
  }
  
  .orderlist-header {
    padding: 1rem 1.25rem;
  }
  
  .orderlist-content {
    padding: 0.75rem;
  }
  
  .orderlist-card {
    padding: 1rem;
  }
  
  .orderlist-product-card {
    width: 120px;
    padding: 0.75rem;
  }
  
  .orderlist-product-image {
    height: 70px;
  }
  
  .orderlist-products-scroll-container {
    margin: 0 -0.75rem;
    padding: 0 0.75rem;
  }
}

/* Empty state grid fix */
.orderlist-empty {
  grid-column: 1 / -1;
  text-align: center;
  padding: 2rem;
}