/* ---------- CONTAINER ---------- */
.otp-container {
  max-width: 420px;
  margin: 80px auto;
  background: #fff;
  padding: 2.5rem 2rem;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  font-family: "Inter", sans-serif;
}

/* ---------- HEADINGS ---------- */
.otp-container h2 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #222;
  margin-bottom: 0.5rem;
}

.otp-subtitle {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 1.8rem;
  line-height: 1.5;
}

.otp-subtitle strong {
  color: #111;
}

/* ---------- INPUT ---------- */
.otp-input {
  width: 100%;
  letter-spacing: 8px;
  text-align: center;
  font-size: 1.5rem;
  padding: 0.9rem;
  border-radius: 12px;
  border: 2px solid #ddd;
  outline: none;
  transition: all 0.2s ease;
  font-weight: 500;
  background-color: #fafafa;
}

.otp-input:focus {
  border-color: #007bff;
  background-color: #fff;
  box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.1);
}

/* ---------- BUTTON ---------- */
.otp-submit-btn {
  margin-top: 1.6rem;
  width: 100%;
  padding: 0.9rem 1rem;
  font-size: 1.05rem;
  font-weight: 600;
  background: linear-gradient(90deg, #007bff, #0056b3);
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.25s ease;
}

.otp-submit-btn:hover {
  background: linear-gradient(90deg, #0056b3, #003d82);
  transform: translateY(-1px);
}

.otp-submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* ---------- STATUS TEXT ---------- */
.otp-status-message {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #777;
}

/* ---------- RESPONSIVE ---------- */
@media (max-width: 480px) {
  .otp-container {
    margin: 50px 20px;
    padding: 2rem 1.5rem;
  }

  .otp-input {
    font-size: 1.3rem;
    letter-spacing: 6px;
  }

  .otp-submit-btn {
    font-size: 1rem;
  }
}
