/* Quotations Overlay */
.quotations-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: quotations-fadeIn 0.3s ease;
  padding: 2rem;
}

@keyframes quotations-fadeIn {
  from { 
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  to { 
    opacity: 1;
    backdrop-filter: blur(8px);
  }
}

/* Quotations Panel */
.quotations-panel {
  width: 100%;
  max-width: 1000px;
  max-height: 90vh;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: quotations-slideUp 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

@keyframes quotations-slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Header */
.quotations-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.quotations-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%);
}

.quotations-header-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin: 0 0 0.25rem 0;
  font-family: 'Inter', sans-serif;
}

.quotations-header-content p {
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
  font-size: 0.9rem;
}

.quotations-close-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  flex-shrink: 0;
}

.quotations-close-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

/* Summary */
.quotations-summary {
  display: flex;
  gap: 2rem;
  padding: 1.5rem 2rem;
  background: white;
  border-bottom: 1px solid #e2e8f0;
  justify-content: center;
}

.quotations-summary-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1.5rem;
  position: relative;
}

.quotations-summary-item:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 30px;
  background: #e2e8f0;
}

.quotations-summary-label {
  font-size: 0.8rem;
  color: #64748b;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.quotations-summary-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
}

/* Empty State */
.quotations-empty {
  text-align: center;
  padding: 4rem 2rem;
  background: white;
}

.quotations-empty-icon {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  opacity: 0.5;
}

.quotations-empty h3 {
  font-size: 1.5rem;
  color: #374151;
  margin: 0 0 1rem 0;
}

.quotations-empty p {
  color: #6b7280;
  margin: 0;
  font-size: 1rem;
}

/* Content */
.quotations-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  background: #f8fafc;
}

.quotations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 1.5rem;
}

/* Quotation Card */
.quotations-card {
  background: white;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #f1f5f9;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.quotations-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.quotations-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
}

/* Card Header */
.quotations-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  gap: 1rem;
}

.quotations-product-info {
  flex: 1;
}

.quotations-product-name {
  font-size: 1.1rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.75rem 0;
  line-height: 1.3;
}

.quotations-status-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  color: white;
  font-size: 0.8rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

.quotations-request-id {
  font-size: 0.8rem;
  color: #64748b;
  background: #f8fafc;
  padding: 0.375rem 0.75rem;
  border-radius: 12px;
  font-weight: 600;
  flex-shrink: 0;
}

/* Card Body */
.quotations-card-body {
  margin-bottom: 1rem;
}

.quotations-date-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #64748b;
  font-size: 0.85rem;
  margin-bottom: 1.25rem;
  padding: 0.75rem;
  background: #f8fafc;
  border-radius: 8px;
}

.quotations-date-icon {
  font-size: 0.9rem;
}

.quotations-actions {
  display: flex;
  justify-content: center;
}

.quotations-download-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.quotations-download-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(16, 185, 129, 0.4);
}

.quotations-download-icon {
  font-size: 1.1rem;
}

.quotations-pending {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: #fff7ed;
  border: 1px solid #fed7aa;
  border-radius: 12px;
  width: 100%;
  text-align: left;
}

.quotations-pending-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.quotations-pending-text {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.quotations-pending-text strong {
  color: #c2410c;
  font-size: 0.95rem;
}

.quotations-pending-text span {
  color: #9a3412;
  font-size: 0.85rem;
}

/* Card Footer */
.quotations-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid #f1f5f9;
  font-size: 0.8rem;
  color: #64748b;
}

.quotations-product-id {
  background: #f1f5f9;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
}

.quotations-file-info {
  display: flex;
  align-items: center;
  gap: 0.375rem;
}

.quotations-file-icon {
  font-size: 0.7rem;
}

/* Footer */
.quotations-footer {
  padding: 1.5rem 2rem;
  background: white;
  border-top: 1px solid #e2e8f0;
  text-align: center;
}

.quotations-close-main-btn {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

.quotations-close-main-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.4);
}

/* Loader */
.quotations-loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  gap: 1.5rem;
  color: #4a5568;
  font-weight: 500;
}

.quotations-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #e2e8f0;
  border-top: 4px solid #667eea;
  border-radius: 50%;
  animation: quotations-spin 1s linear infinite;
}

@keyframes quotations-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Scrollbar */
.quotations-content::-webkit-scrollbar {
  width: 8px;
}

.quotations-content::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.quotations-content::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 4px;
}

.quotations-content::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
}

/* Responsive */
@media (max-width: 768px) {
  .quotations-overlay {
    padding: 1rem;
  }
  
  .quotations-panel {
    max-height: 95vh;
    border-radius: 16px;
  }
  
  .quotations-header {
    padding: 1.25rem 1.5rem;
  }
  
  .quotations-summary {
    padding: 1rem 1.5rem;
    gap: 1rem;
  }
  
  .quotations-summary-item {
    padding: 0 1rem;
  }
  
  .quotations-summary-item:not(:last-child)::after {
    right: -0.5rem;
  }
  
  .quotations-content {
    padding: 1.5rem;
  }
  
  .quotations-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .quotations-card {
    padding: 1.25rem;
  }
  
  .quotations-card-header {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .quotations-request-id {
    align-self: flex-start;
  }
}

@media (max-width: 480px) {
  .quotations-overlay {
    padding: 0.5rem;
  }
  
  .quotations-header {
    padding: 1rem 1.25rem;
  }
  
  .quotations-content {
    padding: 1rem;
  }
  
  .quotations-card {
    padding: 1rem;
  }
  
  .quotations-download-btn {
    padding: 0.875rem 1.5rem;
    font-size: 0.9rem;
  }
  
  .quotations-pending {
    padding: 1rem;
  }
}